.group-card {
    box-sizing: border-box;
    display: flex;
    min-block-size: 200px;
    flex-direction: column;
    background-color: #61dafb;
    padding: 20px;
    border-radius: 20px;
    border: 3px solid #666;
    align-items: center;
    justify-content: center;
}

.group-card__description {
    display: flex;
    justify-content: space-around;
    inline-size: 100%;
}

.group-card__avatar {
    inline-size: 100px;
    block-size: 100px;
    border-radius: 100px;
    border: 2px solid #000;
}